import { Component, OnInit } from '@angular/core';
import { Platform } from '@ionic/angular';
import { Device } from '@ionic-native/device/ngx';
import { Observable, from } from 'rxjs';
import { Trabajador } from 'src/app/entities/trabajador';
import { MenuSSTJsonService } from 'src/app/services/SST/menu-sst-json.service';

import { ContextService } from 'src/app/services/context.service';
import { NetworkService } from 'src/app/services/network.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-permisos',
  templateUrl: './permisos.page.html',
  styleUrls: ['./permisos.page.scss'],
})
export class PermisosPage implements OnInit {
  user: Trabajador;
  optMenuOptions: Observable<any[]>;
  sysConected: Observable<{ connected: boolean; platform: string }>;
  isChecked: boolean = true;

  constructor(
    private context: ContextService,
    private chekInternet: NetworkService,
    public platform: Platform,
    private device: Device,
    private menuConfOptions: MenuSSTJsonService,
    private router: Router
  ) {
    this.platform.backButton.subscribeWithPriority(101, () => {
      this.router.navigate(['/home']);
    });
  }

  ngOnInit() {
    console.log('permisos');
    this.context.get().then(c => {
      this.user = c.trabajador;
      console.log(c);
    });

    this.optMenuOptions = this.menuConfOptions.getMenuSST();
    this.sysConnectedCheked().then(res => {
      this.sysConected = from([res]);
    });
  }

  getUuid(): string {
    return this.device.uuid;
  }

  async sysConnectedCheked(): Promise<{ connected: boolean; platform: string }> {
    let platform = this.platform.platforms()[0];
    let conection = await this.chekInternet.testNetworkConnection();
    if (conection) {
      return { connected: true, platform };
    }
    return { connected: false, platform };
  }

  navergarTo(page: string) {
    this.router.navigate([`/home/permisos/${page}`]);
  }

  changeTitle() {
    this.isChecked = !this.isChecked;
  }

  pushPermisosSST(): void {
    this.router.navigate(['/home']);
  }
}
