import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PermisosPage } from './permisos.page';

const routes: Routes = [
  {
    path: '',
    component: PermisosPage,
  },
  {
    path: 'consultar-permiso',
    loadChildren: () => import('src/app/pages/SST/consultar-permiso/consultar-permiso.module').then(m => m.ConsultarPermisoPageModule),
  },
  {
    path: 'permisos-pendientes',
    loadChildren: () =>
      import('src/app/pages/SST/permisos-pendientes/permisos-pendientes.module').then(m => m.PermisosPendientesPageModule),
  },
  {
    path: 'liberar-trabajadores',
    loadChildren: () =>
      import('src/app/pages/SST/liberar-trabajadores/liberar-trabajadores.module').then(m => m.LiberarTrabajadoresPageModule),
  },
  {
    path: 'registro-permiso',
    loadChildren: () => import('src/app/pages/SST/registro-permiso/registro-permiso.module').then(m => m.RegistroPermisoPageModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PermisosPageRoutingModule {}
